<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2>前端产品</h2>
        <p>2005年以后，互联网进入Web 2.0时代，各种类似桌面软件的Web应用大量涌现，网站的前端由此发生了翻天覆地的变化。网页不再只是承载单一的文字和图片，各种丰富媒体让网页的内容更加生动，网页上软件化的交互形式为用户提供了更好的使用体验，这些都是基于前端技术实现的。</p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  购买数量：
              </div>
              <div class="sales-board-line-right">
                  <v-count :max="buyNumberRange.max" :min="buyNumberRange.min" @onChange="getParamChange('buyNumber',$event)"></v-count>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  产品类型：
              </div>
              <div class="sales-board-line-right">
                  <v-select :selections="buyTypes" @onChange="getParamChange('buyType',$event)"></v-select>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  有效时间：
              </div>
              <div class="sales-board-line-right">
                  <v-choose :selections="periodList" @onChange="getParamChange('buyPeriod',$event)"></v-choose>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  产品版本：
              </div>
              <div class="sales-board-line-right">
                <v-mulchoose :selections="versionList" @onChange="getParamChange('buyVersions',$event)"></v-mulchoose>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  总价：
              </div>
              <div class="sales-board-line-right">
                 {{price}}元
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button" @click="showPayDialog">
                    立即购买
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h3>产品说明</h3>
        <p>前端开发的入门门槛其实非常低，与服务器端语言先慢后快的学习曲线相比，前端开发的学习曲线是先快后慢。所以，对于从事IT工作的人来说，前端开发是个不错的切入点。也正因为如此，前端开发领域有很多自学成“才”的同行，但大多数人都停留在会用的阶段，因为后面的学习曲线越来越陡峭，每前进一步都很难。</p>

        <h3>用户行为指标</h3>
        <ul>
          <li>必须掌握基本的Web前端开发技术，其中包括：CSS、HTML、SEO、DOM、BOM、Ajax、JavaScript等；</li>
          <li>在一名合格的前端工程师的知识结构中，网站性能优化、SEO和服务器端的基础知识也是必须掌握的；</li>
          <li>必须学会运用各种工具进行辅助开发</li>
          <li>除了要掌握技术层面的知识，还要掌握理论层面的知识，包括代码的可维护性、组件的易用性、分层语义模板和浏览器分级支持，等等；</li>
          <li>看似简单的网页制作，如果要做得更好、更专业，真的是不简单。这就是前端开发的特点，也是让很多人困惑的原因。如此繁杂的知识体系让新手学习起来无从下手，对于老手来说，也时常不知道下一步该学什么</li>
        </ul>

        <h3>浏览网站方式</h3>
        <ul>
          <li>用户上网设备类型</li>
          <li>用户浏览器的名称和版本</li>
          <li>访问者电脑分辨率显示模式</li>
          <li>用户所使用的操作系统名称和版本</li>
          <li>用户所在地理区域分布状况等</li>
        </ul>
      </div>
      <mydialog :isShow="isShowPayDialog" @onclose="hidePayDialog">
        <table class="buy-dialog-table">
          <tr>
            <th>购买数量</th>
            <th>产品类型</th>
            <th>有效时间</th>
            <th>产品版本</th>
            <th>总价</th>
          </tr>
          <tr>
            <td>{{ buyNumber }}</td>
            <td>{{ buyType.label }}</td>
            <td>{{ buyPeriod.label }}</td>
            <td>
              <span v-for="item in buyVersions">{{ item.label }}</span>
            </td>
            <td>{{ price }}</td>
          </tr>
        </table>
        <h3 class="buy-dialog-title">请选择银行</h3>
        <bank-choose  @onchange="onChangeBanks"></bank-choose>
        <div class="button buy-dialog-btn" @click="confirmBuy">
          确认购买
        </div>
      </mydialog>
      <mydialog :isShow="isShowErrDialog" @onclose="hideErrDialog">
          支付失败！
      </mydialog>
      <my-check :is-show-check-dialog="isShowCheckOrder" @onclose-check-dialog="hideCheckOrder" :order-id="orderId">
      </my-check>
  </div>
</template>

<script>
import vChoose from '@/components/Chooser'
import vSelect from '@/components/Selection'
import vMulchoose from '@/components/MulChooser'
import vCount from '@/components/Counter'
import dialog from '@/components/Dialog'
import bankChoose from '@/components/BankChooser'
import myCheck from '@/components/CheckOrder'
import _ from 'lodash'
import axios from 'axios'
export default {
  name: 'analysis',
  data () {
    return {
      buyTypes: [
        {
          label: '入门版',
          value: 1
        },
        {
          label: '中级版',
          value: 2
        },
        {
          label: '高级版',
          value: 3
        }
      ],
      periodList:[
        {
          label: '半年',
          value: 1
        },
        {
          label: '一年',
          value: 2
        },
        {
          label: '三年',
          value: 3
        }
      ],
      versionList: [
        {
          label: '客户版',
          value: 1
        },
        {
          label: '代理商版',
          value: 2
        },
        {
          label: '专家版',
          value: 3
        }
      ],
      buyNumberRange:{
        max:20,
        min:1
      },
      isShowPayDialog: false,
      isShowErrDialog:false,
      isShowCheckOrder: false,
      //组件上传的购买参数
      buyNumber:0,
      buyType:{},
      buyVersions:[],
      buyPeriod:{},
      price:0,
      bankId:null,
      orderId:null
      
    }
  },
  components:{
      vChoose,
      vSelect,
      vMulchoose,
      vCount,
      mydialog:dialog,
      bankChoose,
      myCheck
  },
  methods:{
    // 获取子组件传递过来的值
    getParamChange(attr,value){
      this[attr] = value;
      this.getPrice();
    },
    // 计算总价格
    getPrice(){
      // 获取购买版本数组里面的value
      let buyVersionArr=_.map(this.buyVersions,(el)=>{
        return el.value
      })

      // 所有请求参数
      let reqParam={
        buyNumber: this.buyNumber,
        buyType: this.buyType.value,
        buyPeriod: this.buyPeriod.value,
        version: buyVersionArr.join(',')
      }

      // console.log(reqParam);
      //由于github这里本来用post
      axios.get('./static/db.json',{reqParam}).then(res=>{
        // console.log(res.data.appData.getPrice.amount)
        this.price=res.data.getPrice.amount;
      }).catch(err=>{
        console.log(err)
      })

    },
    //关闭打开支付弹窗
    showPayDialog () {
      this.isShowPayDialog = true
    },
    hidePayDialog () {
      this.isShowPayDialog = false
    },
    confirmBuy(){
      let buyVersionArr=_.map(this.buyVersions,(el)=>{
        return el.value
      })
      let reqParam={
        buyNumber: this.buyNumber,
        buyType: this.buyType.value,
        buyPeriod: this.buyPeriod.value,
        version: buyVersionArr.join(','),
        bankId:this.bankId
      }
      //由于github这里本来用post
      axios.get('./static/db.json',{reqParam}).then(res=>{
        // console.log(res.data.appData.createOrder.orderId);
        this.orderId=res.data.createOrder.orderId;
        this.isShowCheckOrder = true
        this.isShowPayDialog = false
      }).catch(err=>{
        console.log(err);
        this.isShowPayDialog = false
        this.isShowErrDialog = true
      })
    },
    //银行选择
    onChangeBanks(bankObj){
      this.bankId = bankObj.id
    },
    hideErrDialog () {
      this.isShowErrDialog = false
    },
    hideCheckOrder () {
      this.isShowCheckOrder = false
    },
  },
  mounted:function(){
    this.buyNumber=1;
    this.buyType=this.buyTypes[0];
    this.buyVersions=[this.versionList[0]];
    this.buyPeriod=this.periodList[0];
    this.getPrice();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th{
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #5CA6DB;
  color: #fff;
  border: 1px solid #5CA6DB;
}
</style>



